
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: 'goodSendStep',
components: {
XInput,
Group
},
data () {
return {
price: '',
stock: '',
baseInfo: {
goodsSkuInfo: []
}
}
},
created () {
this.getBaseInfo()
},
methods: {
check (val,type) {
if(!val || val < 0) {
this[type] = 0
}
},
check2 (val,type,index) {
if(!val || val < 0) {
this.baseInfo.goodsSkuInfo[index][type] = 0
}
},
set () {
this.baseInfo.goodsSkuInfo.map((item)=>{
if(this.price) {
item.memberPrice = this.price
}
if(this.stock) {
item.stock = this.stock
}
})
},
getNextStep () {
this.$http.fetchPost('/merchant/good/add/goods/sku',this.baseInfo).then((res)=>{
console.log(res)
if(res.data.code == 200) {
this.$router.push({
path: 'goodsAddNextStep',
query: {
id: res.data.data.id
}
})
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},
getBack () {
this.$router.go(-1)
},
getBaseInfo() {
this.$http.fetchGet('/merchant/good/get/goods/sku',{goodsId: this.$route.query.id}).then((res)=>{
if(res.data.code == 200) {
this.baseInfo = res.data.data
this.baseInfo.goodsSkuInfo.map((item)=>{
if(!item.memberPrice){
item.memberPrice = 0
}
if(!item.stock){
item.stock = 0
}
})
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},
}
}



